@charset "utf-8";
 /*http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    font-size: 14px;
    line-height: 1.5em;
    font-family: "Droid Sans";
}
ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
/*//end css reset*/

.howtorun{
  list-style: number;
  padding-left: 20px;
}
.maintitle{
  color: #61dafb;
  font-size: 2em;
  margin-bottom: 20px;
}
.lists{
  margin-top: 20px !important;
}
.lists li:nth-child(odd){
  margin-right: 3%;
}
.lists li{
  -webkit-box-shadow: 0 0 8px 0 #ddd;
          box-shadow: 0 0 8px 0 #ddd;
  padding: 12px;
  background: #61dafb;
  margin: 8px 0;
  cursor: pointer;
  width: 43%;
  float: left;
  min-height: 150px;
}
.lists li img{
  display: block;
  margin-top: 8px;
}
.lists li:hover{
  background: cadetblue;
  color: #fff;
}
.flimsApp{
  width: 50%;
  margin: 40px auto;
}
select{
  padding: 8px;
}
input[type="submit"]{
  padding: 9px;
}
@media(max-width: 767px){
  .flimsApp{
    width: 80%;
  }
}

/*form css*/

.mainWrapper{
  background: url(work-banner-demon.jpg) no-repeat center;
  background-size: cover;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.form_wrapper{
  width: 400px;
  margin: 20px auto;
}

.formwrapper {
    width: 100%;
    position: relative;
    height: 35px;
}
form{
  position: relative;
}
.overlay{
  background: rgba(34,48,59,0.9);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 1px solid rebeccapurple;
  display: block;
  z-index: 1;
  position: fixed;
  overflow: auto;
}

.overlaysentBtn{
  text-align: center;
}
.startprojectForm{
  /*background: rgba(34,48,59,0.9);*/
  color: #fff;
  position: relative;
}
.startprojectForm input,.startprojectForm select{
  color: #fff;
}
.startprojectForm select{
  padding: 0;
}
.overlay a{
  text-decoration: none;
}
/*---------------//overlay*/

h1.title {
  font-size: 25px;
  margin-bottom: 28px;
  padding: 0 1em 0 0;
  text-transform: uppercase;
}
input[type="text"]{
  width: 100%;
  height: 35px;    
  /*above line help custum span area to be content editable except "select"*/
  position: absolute;
  border: none;
  border-bottom: 2px solid #000;
  outline: none;
  background: transparent;
}

.startprojectForm  input[type="text"]{
  border-bottom: 2px solid #fff;
}

.spbtn{
  color: #fff!important;
}

.custumSelect:focus{
  outline: 0;
}
.custumText{
  width: 100%;
  height: 35px;
  line-height: 35px;
  background: transparent;
  display: inline-block;
  border-bottom: 1px solid;
}
.custumSelect,.custumText{
  outline: none;
}
.sendBtn,.sendBtn:hover,.sendBtn:focus{
  border: 2px solid;
  color: #fff;
  text-align: center;
  padding: 10px 30px;
  margin-top: 17px;
  width: auto;
  cursor:pointer;
  background: transparent;
}
.closeBtn{
  width: 35px;
  height: 35px;
  border-radius: 30px;
  border: 2px solid #ef3042;
  cursor: pointer;
  position: absolute;
  right: 1.5em;
}

.closeBtn:before,.closeBtn:after{
    content: "";
    position: absolute;
    left: 8px;
    top: 11px;
    width: 20px;
    height: 2px;
    background: #ef3042;
}
.closeBtn:before {
    -ms-transform: translateY(6px) translateX(0) rotate(45deg);
    -webkit-transform: translateY(6px) translateX(0) rotate(45deg);
    transform: translateY(6px) translateX(0) rotate(45deg);    
}

.closeBtn:after {
    -ms-transform: translateY(6px) translateX(0) rotate(-45deg);
    -webkit-transform: translateY(6px) translateX(0) rotate(-45deg);
    transform: translateY(6px) translateX(0) rotate(-45deg);
}
.overlay a{
  text-decoration: none;
}

select {
    width: 100%;
    height: 35px;
    position: absolute;
    z-index: 0;
    background: transparent;
    border: none;
        border-bottom-width: medium;
        border-bottom-style: none;
        border-bottom-color: currentcolor;
    border-bottom: 2px solid #fff;
    outline: none;
}
.form_margin_Ctrl {
    margin: 20px 0;
}
.optioncolor {
    color: #eee;
    opacity: .5;
    -moz-opacity:.5;
    -webkit-opacity: .5;
}
.json_result{
  text-align: center;
  position: absolute;
  z-index: 99;
  width: 100%;
  padding: 20px 0 30px;
}
@media(min-width: 768px){
  .startprojectForm{
      margin: auto;
      width: 50%;
      top: 50%;
      -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
              transform: translateY(-50%);
  }
}
@media(max-width: 767px){
  .startprojectForm{
      margin: 100px auto;
      width: 90%;
  }
}


